import CountDown from '~/components/elements/CountDown';

export default {
	layout: 'layout-blank',
	components: {
		CountDown
	},
	created: function () {
		document.querySelector( 'body' ).classList.contains( 'mmenu-active' ) &&
			document.querySelector( 'body' ).classList.remove( 'mmenu-active' )

	}
};
